import React, { useContext, useEffect } from "react";
import "./ImportantTransaction.css";
import HeadSubHead from "../RoutePagesHead_SubHead/HeadSubHead";
import RouteSearchSort from "../RoutePagesSearch_Sort/RouteSearchSort";
import { UserAuthContext } from "../../../Contexts/UserAuthContext";
import AccessDenied from "../../AccessDenied";
import Footer from "../../Footer/Footer";
import UserTransactionDataThumbnail_History_Thread from "../../UserTransactionDataThumbnailHistoryThread/UserTransactionDataThumbnail_History_Thread";
import { UserTransactionDataContext } from "../../../Contexts/UserTransactionDataContext";
import NoTransactionEntry from "../../NoTransactionEntry";
import { AppThemeContext } from "../../../Contexts/AppThemeContext";

function ImportantTransaction() {
	const { isUserLoggedIn } = useContext(UserAuthContext);
	const { selectedTheme } = useContext(AppThemeContext);
	const { importantTransactions } = useContext(UserTransactionDataContext);

	useEffect(() => {
		document.title = "Important Transactions - TODO ";
		const searchSortAddBtnDOM = document.querySelector(".search-sort-add-item");
		window.addEventListener("scroll", () => {
			//console.log(window.scrollY);
			if (window.scrollY > 200) {
				if (searchSortAddBtnDOM)
					searchSortAddBtnDOM.classList.add(
						"search-sort-add-operation-container__fixed"
					);
			} else {
				if (searchSortAddBtnDOM) {
					searchSortAddBtnDOM.classList.remove(
						"search-sort-add-operation-container__fixed"
					);
				}
			}
		});
	});

	console.log(importantTransactions);
	return isUserLoggedIn ? (
		<React.Fragment>
			<div className="main-bg-container">
				<div className="important-transaction-container">
					<div className="container">
						<HeadSubHead
							head="You Have Marked These Transactions as Important"
							subhead="You will be notified frequently for all important transactions"
						/>
						<div
							className="d-flex search-sort-add-item justify-content-center align-items-center"
							style={{
								background: selectedTheme == "light" ? "#fff" : "#1a1a1a",
							}}
						>
							<RouteSearchSort />
						</div>
						<div className="mt-5 transaction-container">
							{importantTransactions ? (
								importantTransactions.map((item) => {
									return (
										<UserTransactionDataThumbnail_History_Thread
											name={item.name}
											id={item.id}
											key={item.id}
											description={item.description}
											isImportant={item.isImportant}
											transactionDate={item.transactionDate}
											amount={item.amount}
										/>
									);
								})
							) : (
								<NoTransactionEntry />
							)}
						</div>
					</div>
				</div>
			</div>
			<Footer />
		</React.Fragment>
	) : (
		<AccessDenied />
	);
}

export default ImportantTransaction;



